.card {
    position: relative;
    cursor: pointer;
}

.card:hover .card__img {
    animation: opacity 0.2s ease-in-out;
}

@keyframes opacity {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0.8;
    }
    100% {
        opacity: 1;
    }
}

.card:hover .card__icon {
    animation: slide 0.5s ease-in-out;
}

@keyframes slide {
    0% {
        left: 0;
    }
    50% {
        left: -20px;
    }
    100% {
        opacity: 0;
    }
}

.card__link {
    position: absolute;
    z-index: 1;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;

}

.card__img {
    margin-bottom: 28px;
    border-radius: 20px;
}

.card__body {
    display: flex;
    justify-content: space-between;
    column-gap: 10px;
    align-items: center;
}

.card__title {
    font-weight: 500;
    font-size: 32px;
    letter-spacing: -0.04em;
}

.card__description {
    font-weight: 500;
    font-size: 24px;
    letter-spacing: -0.04em;
    color: var(--gray);
}

.card__icon {
    position: relative;
}

@media screen and (max-width: 1279px) {
    .card__title {
        font-size: 24px;
    }
    .card__description {
        font-size: 18px;
    }
}

@media screen and (max-width: 1023px) {
    .card__title {
        font-size: 32px;
    }
    .card__description {
        font-size: 24px;
    }
}

@media screen and (max-width: 767px) {
    .card__title {
        font-size: 24px;
    }
    .card__description {
        font-size: 18px;
    }
}